@import url(https://fonts.googleapis.com/css2?family=Londrina+Solid:wght@300&display=swap);
.card-front {
  height: 110px;
  width: 110px;
  background-color: #c0c0c0;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
  border-radius: 5px;
}

.card-front:hover {
  cursor: pointer;
}

.card-back {
  height: 110px;
  width: 110px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 5px;
  overflow: hidden;
  border-radius: 5px;
}

.card-image {
  height: 100%;
  width: 90%;
}

.message {
  font-size: 20px;
  font-family: "Londrina Solid", cursive;
}

.board {
  margin: auto auto;
  height: 95vh;
  max-width: 750px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  font-family: "Londrina Solid", cursive;
  background-color: #888;
  background-image: url("https://www.mariomayhem.com/fun/smb_level_editor/images/overworld_bg.png");
  background-size: 100% 100%;
  border-radius: 5%;
  margin-left: 340px;
  letter-spacing: 1px;
}

.header {
  display: flex;
  flex-direction: column;
}

.title {
  text-align: center;
  font-size: 30px;
  margin-bottom: -40px;
}

.messages {
  display: flex;
  flex-direction: row;
  font-size: 20px;
  margin: 0;
}

.startButton {
  display: center;
  background-color: #d10d0d;
  margin-bottom: 5px;
  padding: 5px;
  font-family: "Londrina Solid", cursive;
  font-size: 24px;
  color: #fff;
  border-radius: 15px;
  opacity: 0.8;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer;
}

.startButton:hover {
  opacity: 1;
  box-shadow: 3px 3px #363636;
}

.startButton:focus {
  outline: none;
}

.startButton:active {
  -webkit-transform: scale(0.9, 0.9);
          transform: scale(0.9, 0.9);
}

.mainBody {
  max-width: 750px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

footer {
  display: flex;
  margin-right: 75px;
}

.message1 {
  font-size: 20px;
  font-family: "Londrina Solid", cursive;
  margin-left: 30px;
}

.restartButton {
  height: 40px;
  width: auto;
  margin-left: 30px;
  margin-top: 10px;
  opacity: 0.8;
  font-family: "Londrina Solid", cursive;
  font-size: 24px;
  color: #fff;
  background-color: #d10d0d;
  border-radius: 15px;
  cursor: pointer;
}

.restartButton:hover {
  opacity: 1;
  box-shadow: 3px 3px #363636;
}

.restartButton:focus {
  outline: none;
}

.restartButton:active {
  -webkit-transform: scale(0.9, 0.9);
          transform: scale(0.9, 0.9);
}

@-webkit-keyframes spinIn {
  0% {
    -webkit-transform: scale(0, 0);
            transform: scale(0, 0);
  }
  50% {
    -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
  }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
}

@keyframes spinIn {
  0% {
    -webkit-transform: scale(0, 0);
            transform: scale(0, 0);
  }
  50% {
    -webkit-transform: scale(1.5, 1.5);
            transform: scale(1.5, 1.5);
  }
  100% {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
}

@-webkit-keyframes spinOut {
  from {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
  to {
    -webkit-transform: scale(0, 0);
            transform: scale(0, 0);
  }
}

@keyframes spinOut {
  from {
    -webkit-transform: scale(1, 1);
            transform: scale(1, 1);
  }
  to {
    -webkit-transform: scale(0, 0);
            transform: scale(0, 0);
  }
}

